function RandomColor() {
    let T = this;
    T.p = []; // 

    /**
     * 
     * @returns string R/G/B 
     */
    T.generate = function () {
        let c = [0, 0, 0];
        c[0] = Math.floor(Math.random() * 255);
        c[1] = Math.floor(Math.random() * 255);
        c[2] = Math.floor(Math.random() * 255);
        return c;
    }

    /**
     * 
     * @returns string R/G/B 
     */
    T.dissimilarRgb = function () {
        // 
        let c = [0, 0, 0];
        if (T.p.length === 0) {
            c = T.generate();
            T.p.push(c);
            return c;
        }

        // 
        let f = gFalse,
            t = 0,
            d = [0, 0, 0];

        //  20
        while (f === gFalse && t < 20) {
            c = T.generate();
            // 
            let idx = 0;
            for (let i = 0; i < T.p.length; i++) {
                idx = i;
                d[0] = (T.p[i][0] - c[0]) / 256;
                d[1] = (T.p[i][1] - c[1]) / 256;
                d[2] = (T.p[i][2] - c[2]) / 256;
                //  RGB 0.3 
                if (Math.sqrt(d[0] * d[0] + d[1] * d[1] + d[2] * d[2]) < 0.3)
                    break;
            }

            // 
            if (idx === T.p.length) {
                T.p.push(c);
                f = gTrue;
            }
            t++;
        }
        return c;
    }

    /**
     *  rgba 
     * @param rgb RGB 
     * @param opacity 0:1:
     */
    T.format = function (rgb, opacity) {
        return "rgba(" + rgb[0] + ", " + rgb[1] + ", " + rgb[2] + ", " + opacity + ")";
    }

    /**
     * 
     */
    T.reset = function () {
        VLOOL.color.p.length = 0;
    }
}

