/**
 * 
 */
function Stopwatch() {
    let T = this;
    T.sT = null; // 
    T.lT = null; // 

    /**
     * 
     */
    T.reset = function () {
        T.sT = new Date().getTime();
    }

    /**
     * 
     */
    T.lapStart = function (t) {
        if (t !== gUndefined)
            LOG(t);
        T.lT = new Date().getTime();
    }

    /**
     * 
     * @param pre 
     * @param s true
     */
    T.lapStop = function (pre, s) {
        let c = new Date().getTime() - T.lT;
        // 
        if (s !== gTrue) {
            let msg = pre + " " + c + " ms";
            if (c < 300)
                INFO(msg);
            else if (c < 500)
                WARN(msg);
            else
                ERR(msg);
        }
        return c;
    }

    /**
     * 
     */
    T.stop = function () {
        return new Date().getTime() - T.sT;
    }

    // 
    T.reset();
}

