/**
 * 
 * @param mode 
 */
function WelcomePage(mode) {
    let T = this,
        _ui = ".v-welcome-page";
    T.ui = $(_ui); // 
    T.button = $(_ui + " > .v-loading"); // 
    T.tips = $(_ui + " > .v-tips"); // 
    T.finished = gFalse; // 
    T.mode = mode; // 

    /**
     * 
     */
    T.done = function () {
        // 
        T.button.unbind(s_Click).click(function () {
            T.close();
        });

        T.ui.css(s_Cursor, "default");
        T.stopAni();

        T.tips.css(s_Animation, s_None);

        T.updateCloseButton(null);
        T.button.addClass("v-btn-done");

        T.finished = gTrue;

        // auto 
        if (T.mode === s_Auto)
            T.autoClose();
        // wait 
        else if (T.mode === "wait")
            T.button.addClass("wait");
    }

    /**
     * 
     */
    T.autoClose = function () {
        let delaySecs = 3,
            closeTimer = null;

        __timeToClose();
        function __timeToClose() {
            T.updateCloseButton(delaySecs);
            delaySecs--;
            if (delaySecs < 0) { // 
                clearTimeout(closeTimer);
                T.close();
            }
            else
                closeTimer = setTimeout(__timeToClose, 1000);
        }
    }

    /**
     * 
     * @param sec 
     */
    T.updateCloseButton = function (sec) {
        T.button.html([
            "",
            // "",
            "Start Reading",
            // "Commence  Lire",
            // "Lesen Starten",
            // "Haga Clic Para Comenzar",
            // " ",
            // "",
            // "    "
        ][V_lang_id]
            + (sec == null ? "" : " <span> (" + sec + "s)</span>"));
    }

    /**
     * 
     */
    T.stopAni = function () {
        T.tips.css(s_Animation, s_None);
    }

    /**
     * 
     */
    T.close = function () {
        T.ui.hide();
        V_doc_scroll_unfreeze();
    }

    /**
     * 
     * @param code 
     * @param combKeys  Ctrl / Shift / Alt / Win / Cmd
     */
    T.disposeHotkey = function (code, combKeys) {
        if (T.finished === gFalse || T.ui.isHidden())
            return;

        switch (code) {
            case 13: // ENTER
                // 
                T.close();
                break;
        }
    }

    // 
    if (T.mode === s_None)
        T.close();
    else
        V_ui_show(T.ui);
}

