/**
 * 
 * @param mask 
 */
function InfoTips(mask) {
    let T = this;
    T.ui = $(".v-info-tips");
    T.aniTimer = null;

    // 
    T.ui.unbind(s_Click).click = function () {
        T.hide();
    }

    // 
    T.mask = mask;
    T.mask.bindPartner(this, T.ui);

    /**
     * 
     * @param message 
     * @param delay  ms 
     * @param mask 
     * @param target 
     */
    T.inform = function (message, delay, mask, target) {
        T.show(message, delay, mask, "inform", target);
    }

    /**
     * 
     * @param message 
     * @param delay  ms 
     * @param mask 
     * @param target 
     */
    T.bubble = function (message, delay, mask, target) {
        T.show(message, delay, mask, "bubble", target);
    }

    /**
     * 
     * @param message 
     * @param delay  ms 
     * @param mask 
     * @param target 
     */
    T.error = function (message, delay, mask, target) {
        T.show(message, delay, mask, "error", target);
    }

    /**
     * 
     * @param message 
     * @param delay  ms 
     * @param mask 
     * @param type info/error
     * @param target 
     */
    T.show = function (message, delay, mask, type, target) {
        clearTimeout(T.aniTimer);

        T.ui.html(message);

        // 
        T.ui.removeClass("error");
        T.ui.removeClass("bubble");
        T.ui.css(s_Width, "")
            .css(s_Height, "")
            .css(s_Right, s_Auto)
            .css(s_Bottom, s_Auto)
            .css(s_BorderRadius, s_varRB);
        if (type === "error")
            T.ui.addClass("error");
        else if (type === "bubble")
            T.ui.addClass("bubble");

        // 
        if (target !== gUndefined) {
            V_ui_moveToTarget(T.ui, target);
        }
        // 
        else
            V_ui_moveToCenter(T.ui);

        // V_ui_show(T.ui);
        T.ui.show();

        // 
        if (delay != null) {
            T.aniTimer = setTimeout(function () {
                    T.hide();
                }, delay);
        }

        // 
        if (mask === gTrue)
            T.mask.show();
    }

    /**
     * 
     */
    T.hide = function () {
        T.ui.hide();
        T.mask.hide();
    }

    /**
     * 
     * @param code 
     * @param combKeys  Ctrl / Shift / Alt / Win / Cmd
     */
    T.disposeHotkey = function (code, combKeys) {
        if (T.ui.isHidden())
            return;

        switch (code) {
            case 27: // ESC
                T.hide();
                break;
        }
    }
}

